<template>
  <v-container
    fill-height
    fluid
    grid-list-xl>
    <notifications
      class="mt-1"
      group="flush"
      position="top center"
    />
    <v-layout
      justify-center
      wrap
    >
      <v-flex
        md8
        xs12
      >
        <material-card
          color="green"
          text="..."
          title="修改密码"
        >
          <v-form>
            <v-container py-0>
              <v-layout wrap>

                <v-flex
                  md12
                  xs12
                >
                  <v-text-field
                    v-model="oP"
                    class="purple-input"
                    label="原密码"
                    type="password"/>
                </v-flex>
                <v-spacer/>

                <v-flex
                  md6
                  xs12
                >
                  <v-text-field
                    v-model="nP"
                    class="purple-input"
                    label="新密码"
                    type="password"/>
                </v-flex>

                <v-flex
                  md6
                  xs12
                >
                  <v-text-field
                    v-model="nP2"
                    class="purple-input"
                    label="确认新密码"
                    type="password"/>
                </v-flex>

                <v-flex
                  text-xs-right
                  xs12
                >
                  <v-btn
                    class="mx-0 font-weight-light"
                    color="success"
                    @click="changePassword"
                    @keyup.enter="changePassword"
                  >
                    更新
                  </v-btn>
                </v-flex>
              </v-layout>
            </v-container>
          </v-form>
        </material-card>
      </v-flex>
      <v-flex
        md4
        xs12
      >
        <material-card class="v-card-profile">
          <v-avatar
            slot="offset"
            class="mx-auto d-block"
            size="130"
          >
            <img
              src="//www.gravatar.com/avatar/ba7eacd36a440d894441cf83d6245b38?s=200"
            >
          </v-avatar>
          <v-card-text class="text-xs-center">
            <h6 class="category text-gray font-weight-thin mb-3"/>
            <h4 class="card-title font-weight-light">显示用户名</h4>
            <p class="card-description font-weight-light">显示邮箱/权限</p>
          </v-card-text>
        </material-card>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
let qs = require('qs')

export default {
  data: () => ({
    oP: null,
    nP: null,
    nP2: null
  }),
  methods: {
    changePassword: function () {
      let vm = this

      if (vm.$data.nP != null && vm.$data.nP !== vm.$data.nP2) {
        vm.$notify({
          group: 'flush',
          type: 'error',
          text: '两次新密码不正确!'
        })
        return
      }
      vm.$http.post(vm.$gc.H + '/sys/change_pwd', qs.stringify({
        oPwd: vm.$data.oP,
        nPwd: vm.$data.nP })
      )
        .then((response) => {
          vm.$notify({
            group: 'flush',
            type: 'info',
            duration: 8000,
            text: response.data.details
          })
        })
    }
  }
}
</script>
